<template>
  <div>
      <v-container class="text-center">
          <Searcher :searcher-string="searcherString"
                    @searcher-string="onSearcherString"
                    @copy-to-clipboard="copyToClipboard"
          ></Searcher>
          <OptionsChooser :option-names="dictionaries"
                          has-all
                          @chosen-dictionaries="onChosenDictionaries"
          ></OptionsChooser>
      </v-container>
      <v-container >
          <div class="search-results" ref="search_results">
              <template v-for="({ dictName, lines }) in linesMatchingArray">
                  <h2>{{ dictName }}</h2>
                  <SearchResults :searcher-string="searcherString"
                                 v-if="lines"
                                 :key="dictName"
                                 :lines-matching="lines"
                  ></SearchResults>
                  ====================
              </template>
          </div>
      </v-container>
  </div>
</template>

<script>
import appBarTitleMixin from '@/mixins/appBarTitleMixin';
import copyToClipboardMixin from '@/mixins/copyToClipboardMixin';
import Searcher from '@/components/turoyoCorpusSearch/Searcher';
import SearchResults from '@/components/turoyoCorpusSearch/SearchResults';
import OptionsChooser from '@/components/OptionsChooser';
import { mapGetters } from 'vuex';

export default {
    name: "Dictionaries",
    mixins: [
        appBarTitleMixin,
        copyToClipboardMixin,
    ],
    components: {
        Searcher,
        SearchResults,
        OptionsChooser
    },
    data() {
        return {
            appBarTitle: 'Dictionaries of Turoyo',
            searcherString: '',
        }
    },
    computed: {
        ...mapGetters(['turoyoDictionariesLinesMatching', 'turoyoChosenDictionaries']),
        ...mapGetters({ dictionaries: 'turoyoDictionaries' }),
        query() {
            return this.turoyoChosenDictionaries + this.searcherString;
        },
        linesMatchingArray() {
            return this.turoyoDictionariesLinesMatching.map(({ dictValue, lines }) => ({
                lines,
                dictName: this.dictNameFromValue(dictValue),
            }))
        },
        dictNameFromValue() {
            return (dictValue) => this.dictionaries.find((el) => el.value === dictValue).name;
        }
    },
    watch: {
        query() {
            this.setQuery();
        },
    },
    created() {
        this.$store.commit('initializeTuroyoDictionaries');
        this.$store.commit('setTuroyoDictionariesSearcherString', '');

    },
    mounted() {
        if (this.$route.query.search) {
            this.onSearcherString(this.$route.query.search);
        }
        if (this.$route.query.dicts) {
            const chosenDicts = this.$route.query.dicts.split('_');
            const mutatedDicts = this.dictionaries.map((dictObj) => ({
                ...dictObj,
                isChosen: chosenDicts.includes(dictObj.value),
            }));
            this.$store.commit('setTuroyoChosenDictionaries', chosenDicts);
            this.$store.commit('setTuroyoDictionaries', mutatedDicts);
        }
    },
    methods: {
        onChosenDictionaries(values) {
            this.$store.commit('setTuroyoChosenDictionaries', values);
            this.setQuery();
        },
        onSearcherString(searcherString) {
            this.searcherString = searcherString;
            this.setQuery();
            this.$store.commit('setTuroyoDictionariesSearcherString', searcherString);
        },
        setQuery() {
            const searchPart = this.searcherString ? { search: this.searcherString } : {};
            const dictsPart = this.turoyoChosenDictionaries ? { dicts: this.turoyoChosenDictionaries.join('_') } : {};
            const query = { ...searchPart, ...dictsPart };
            if (_.isEqual(query, this.$route.query)) {
                return;
            }
            this.$router.push({ query });
        }
    }
}
</script>

