<template>
    <div class="container d-flex justify-center">
        <v-btn v-for="({ name, value }) in optionNames"
               :key="value"
               @click="toggle(value)"
               depressed large tile
               :outlined="chosen(value)"
        >{{ name }}</v-btn>

        <v-btn v-if="hasAll"
               @click="toggleAll"
               depressed large tile
               :outlined="areAllChosen"
        >All</v-btn>
    </div>
</template>

<script>
export default {
    name: "OptionsChooser",
    props: {
        optionNames: { type: Array, default: () => [] },
        hasAll: { type: Boolean },
    },
    data() {
        return {
            chosenValues: [],
            activeColor: 'blue-grey',
            activeAllColor: 'cyan darken-2'
        }
    },
    computed: {
        areAllChosen() {
            return this.chosenValues.length === this.optionNames.length;
        }
    },
    watch: {
        optionNames: {
            deep: true,
            handler() {
                this.chosenValues = this.optionNames.filter(({ isChosen }) => isChosen).map(({ value }) => value);
            }
        }
    },
    methods: {
        chosen(value) {
            return this.chosenValues.includes(value);
        },
        toggle(value) {
            if (this.chosenValues.includes(value)) {
                this.chosenValues = this.chosenValues.filter((el) => el !== value);
            } else {
                this.chosenValues = [...this.chosenValues, value];
            }
            this.emitValues();
        },
        toggleAll() {
            if (this.chosenValues.length < this.optionNames.length) {
                this.chosenValues = this.optionNames.map(({ value }) => value);
            } else {
                this.chosenValues = [];
            }
            this.emitValues();
        },
        emitValues() {
            this.$emit('chosen-dictionaries', this.chosenValues);
        }
    },
    created() {
        this.chosenValues = this.$store.getters.turoyoChosenDictionaries;
    }
}
</script>

<style scoped>
/deep/.v-btn__content {
    width: 7rem;
}
/deep/.v-btn:not(.v-btn--outlined) {
    border: thin solid white;
}
</style>
